import {
  httpRequestUrl
} from "@/api/baseUrl";
import {
  requestServer,
  httpRequest
} from "@/api/baseApi";

/**
 * 
 * @param username  
 * @param password  
 * @param type  
 */
export const loginIn = (username, password) => {
  let params = {
    username: username,
    password: password,
    type: 2  //2
  }
  return httpRequest(`${httpRequestUrl}` + "/sysUser/login", "post", params)
}

